import {
  DeleteColumnsParams,
  InsertColumnsParams,
  InsertColParams,
  MergeColumnsParams,
  SetCellDataParams,
} from '../types';

export const ACTION_MERGE_COLUMNS = 'columns_merge_columns';
export const ACTION_INERT_COLUMNS = 'columns_insert_columns';
export const ACTION_INERT_COL = 'columns_insert_col';
export const ACTION_DELETE_COLUMN = 'columns_delete_column';
export const ACTION_CLEANUP_COLUMN = 'columns_cleanup_column';
export const ACTION_REMOVE_INVALID_CELLS = 'columns_remove_invalid_cells';
export const ACTION_SET_CELL_DATA = 'columns_set_cell_data';

export function mergeColumns(payload: MergeColumnsParams) {
  return {
    type: ACTION_MERGE_COLUMNS,
    payload,
  };
}

export function insertColumns(payload: InsertColumnsParams) {
  return {
    type: ACTION_INERT_COLUMNS,
    payload,
  };
}

export function insertCol(payload: InsertColParams) {
  return {
    type: ACTION_INERT_COL,
    payload,
  };
}

export function deleteColumn(payload: DeleteColumnsParams) {
  return {
    type: ACTION_DELETE_COLUMN,
    payload,
  };
}

export function cleanUpColumns() {
  return {
    type: ACTION_CLEANUP_COLUMN,
  };
}

export function removeInvalidCells() {
  return {
    type: ACTION_REMOVE_INVALID_CELLS,
  };
}

export function setCellData(payload: SetCellDataParams) {
  return {
    type: ACTION_SET_CELL_DATA,
    payload,
  };
}
